<style>
  @import "../../../../static/style/file_upload.css";
  .choose-store-table-wrap {
    width: 500px;
    border: 1px solid #ececec;
    border-radius: 4px;
  }
</style>

<template>
  <div class="add-form-wraper">
    <el-form :model="addParamMsg" ref="ruleForm" label-width="160px" >
      <el-form-item label="选择区域">
        <div>{{addParamMsg.regionName}}</div>
      </el-form-item>
      <el-form-item label="弹窗名称">
        <div>{{addParamMsg.adName}}</div>
      </el-form-item>
      <el-form-item label="开始时间">
        <div>{{addParamMsg.startDateTime}}</div>
      </el-form-item>
      <el-form-item label="结束时间">
        <div>{{addParamMsg.endDateTime}}</div>
      </el-form-item>
      <el-form-item label="上传图片">
        <div>
          <img v-if="addParamMsg.adImgUrl" :src="addParamMsg.adImgUrl" alt="" width="90" height="120">
        </div>
      </el-form-item>
      <el-form-item label="跳转路径">
        <div> {{addParamMsg.adTypeDesc}} </div>
        <div v-if="addParamMsg.adType === 'TOPIC' && addParamMsg.adLinkName">
          {{addParamMsg.adLinkName}}
        </div>
        <div v-if="addParamMsg.adType === 'CATEGORY' && addParamMsg.adLinkName">
          {{addParamMsg.adLinkName}}
        </div>
        <div v-if="addParamMsg.adType === 'GOODS' && addParamMsg.adLinkName">
          {{addParamMsg.adLinkName}}
        </div>
        <div v-if="addParamMsg.adType === 'SECKILLGOODS' && addParamMsg.adLinkName">
          {{addParamMsg.adLinkName}}
        </div>
        <div v-if="addParamMsg.adType === 'BESTSELLERGOODS' && addParamMsg.adLinkName">
          {{addParamMsg.adLinkName}}
        </div>
        <div v-if="addParamMsg.adType === 'URL'">
          <span class="label-txt-wrap">输入路径: </span> 
          <span>{{addParamMsg.adLinkUrl}}</span>
        </div>
        <div v-if="addParamMsg.adType === 'APPLET'">
          <div>
            <span class="label-txt-wrap">选择程序: </span> 
            <span>{{addParamMsg.adLinkIdToStr}}</span>
          </div>
          <div>
            <span class="label-txt-wrap">输入路径: </span> 
            <span>{{addParamMsg.adLinkUrl}}</span>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="应用门店">
        <div v-if="storeCheckedList.length > 0" class="choose-store-table-wrap">
          <el-table
            class="fixed-height"
            :data="storeCheckedList"
            empty-text="暂无选中任何门店"
            max-height="300"
            size="small">
            <el-table-column min-width="170px" label="门店编码/名称">
              <template slot-scope="scope">
                <div>
                  {{scope.row.storeNo}}_{{scope.row.storeName}}
                </div>
              </template>
            </el-table-column>
            <el-table-column min-width="100" prop="cityName" label="城市"></el-table-column>
            <el-table-column min-width="100" prop="saleTypeStr" label="门店类型"></el-table-column>
          </el-table>
        </div>
      </el-form-item>
      <hr class="s-slip-tb">
      <el-form-item>
        <el-button size="small" @click="doCancelHandel">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  import constant from "./constant"
  const requestUrl = {
    // 详情-基本信息
    popupAdDetail: '/plateform/p/client/mall/popup/ad/popupAdDetail',
    // 详情-门店信息
    popupAdStoreList: '/plateform/p/client/mall/popup/ad/popupAdStoreList'
  }
  export default {
    data() {
      return {
        adId: '',
        addParamMsg: {
          DIFFCITY: true,
          regionCode: '', 
          regionName: '', 
          adName: '',
          startDateTime: '',
          endDateTime: '',
          adType: '',
          adTypeDesc: '',
          adLinkUrl: '',
          adLinkName: '',
          adLinkId: '',
          adLinkIdToStr: '', // 跳转指定小程序专用
          storeNoList: [],
          adImgUrl: ''
        },
        storeCheckedList: [],
        appIdOptions: constant.appIdOptions
      }
    },
    created() {
      this.adId = this.$route.params.id
      this.getDetail()
      this.getDetailStoreList()
    },
    methods: {
      getDetail() {
        utils.showLoading()
        this.$http.post(requestUrl.popupAdDetail, {adId: this.adId, DIFFCITY: true}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              this.adId = res.data.adId || ''
              this.addParamMsg.regionCode = res.data.regionCode || ''
              this.addParamMsg.regionName = res.data.regionName || ''
              this.addParamMsg.adName = res.data.adName || ''
              this.addParamMsg.startDateTime = res.data.startDateTime || ''
              this.addParamMsg.endDateTime = res.data.endDateTime || ''
              this.addParamMsg.adType = res.data.adType || ''
              this.addParamMsg.adTypeDesc = res.data.adTypeDesc || ''
              this.addParamMsg.adLinkUrl = res.data.adLinkUrl || ''
              this.addParamMsg.adLinkName = res.data.adLinkName || ''
              this.addParamMsg.adLinkId = res.data.adLinkId || ''
              if(this.addParamMsg.adLinkId && this.addParamMsg.adType === "APPLET") {
                this.addParamMsg.adLinkIdToStr = this.appIdOptions.filter(item => {
                  return item.value === this.addParamMsg.adLinkId
                })[0].label
              }
              this.addParamMsg.adImgUrl = res.data.adImgUrl || ''
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      getDetailStoreList() {
        utils.showLoading()
        this.$http.post(requestUrl.popupAdStoreList, {adId: this.adId, DIFFCITY: true}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            this.storeCheckedList = res.data && res.data.length ? res.data : []
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      doCancelHandel() {
        this.$confirm('确定取消吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/ad')
        }).catch(() => {})
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
  }
</script>


